<template>
    <div class="dataList">
        <el-card>
            <div id="card1"></div>
        </el-card>
        <el-card>
            <div id="card2"></div>
        </el-card>
    </div>
</template>
<script>
import {dataview} from "@/api/api"
export default {
    data() {
        return {

        }
    },
    methods:{
        draw(legend,xAxis,series){
            let myChart2 = this.$echarts.init(document.getElementById('card2'))
            let option = {
                title:{
                    text:'会话量'
                },
                legend:{
                    data:legend
                },
                xAxis:{
                    type:'category',
                    data:xAxis
                },
                yAxis:{
                    type:'value'
                },
                series:series
            }
            myChart2.setOption(option)
        }
    },
    created(){
        dataview().then((res)=>{
            if(res.data.status===200){
                let {legend,xAxis,series} = res.data.data
                this.draw(legend,xAxis,series)
            }
        }).catch(err=>{
            throw err
        })
       
    },
    mounted() {
        let myChart = this.$echarts.init(document.getElementById('card1'))
        myChart.setOption({
            title: {
                text: '班级人数'
            },
            tooltip: {},
            xAxis: {
                data: ['一班', '二班', '三班', '四班', '五班', '六班']
            },
            yAxis: {},
            series: {
                name: '人数',
                type: 'bar',
                data: [30, 40, 54, 36, 28, 45]
            }
        })
    }
}
</script>

<style lang="less">
.dataList {
    width: 100%;
    display: flex;
    justify-content: center;
    .el-card {
        width: 50%;
        #card1,
        #card2 {
            height: 500px;
        }
    }

}</style>